.login-register-container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100vw;
  /* 加载背景图 */
  /*background-image: url('./images/login_background1.jpg');*/
  /* 背景图垂直、水平均居中 */
  background-position: center center;
  /* 背景图不平铺 */
  background-repeat: no-repeat;
  /* 当内容高度大于图片高度时，背景图像的位置相对于viewport固定 */
  background-attachment: fixed;
  /* 让背景图基于容器大小伸缩 */
  background-size: cover;
  background-attachment: fixed;

  /* 登录注册容器 */

  .login-register-box {
    width: 100%;
    max-width: 36em;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);

    /* 登录相关 */

    .login-box {
      position: relative;
      top: 0;
      opacity: 1;
      float: left;
      padding: 4em 4em 4em 3.6em;
      width: 100%;
      background: #fff;
      border-radius: 0.7em;
      transform: scale(1);
      -webkit-transform: scale(1);
      -ms-transform: scale(1);
      z-index: 5;
      transition: 400ms cubic-bezier(.4, 0, .2, 1);
      -webkit-transition: 400ms cubic-bezier(.4, 0, .2, 1);
      -ms-transition: 400ms cubic-bezier(.4, 0, .2, 1);
    }


    /* 登录相关 */

    .ant-input {
      outline: none !important;
      z-index: 10;
      border: none !important;
    }

    /*  antd 文本框去除边框*/

    .ant-input:focus {
      border: none;
      /* border-bottom: 1px solid white !important;*/
      box-shadow: none;
    }

    /** 登录注册公共区域 **/

    .title {
      width: 100%;
      float: left;
      line-height: 1.5em;
      font-size: 2.6em;
      font-weight: 700;
      letter-spacing: 0.2em;;
      color: #ED2553;
      position: relative;
    }

    .title:before {
      content: "";
      width: 0.2em;
      height: 100%;
      position: absolute;
      top: 0;
      left: -1.4em;
      background: #ED2553;
    }

    .input,
    .input label,
    .input input,
    .input .spin,
    .button,
    .button.login button {
      outline: none;
      transition: 300ms cubic-bezier(.4, 0, .2, 1);
      -webkit-transition: 300ms cubic-bezier(.4, 0, .2, 1);
      -ms-transition: 300ms cubic-bezier(.4, 0, .2, 1);
    }


    .input,
    .input label,
    .input input,
    .input .spin,
    .button,
    .button button {
      width: 100%;
      float: left;
    }

    .input,
    .button {
      margin-top: 30px;
      height: 70px;
    }

    .input,
    .input input,
    .button,
    .button button {
      position: relative;
    }

    .input input {
      height: 60px;
      top: 10px;
      background: transparent;
    }

    .input input,
    .input label,
    .button button {
      font-family: 'Roboto', sans-serif;
      font-size: 24px;
      color: rgba(0, 0, 0, 0.8);
      font-weight: 300;
      outline: none;
    }

    .input:before,
    .input .spin {
      width: 100%;
      height: 0.1em;
      position: absolute;
      bottom: 0;
      left: 0;
    }

    .input:before {
      content: "";
      background: rgba(0, 0, 0, 0.1);
      z-index: 3;
    }

    .input .spin {
      background: #ED2553;
      z-index: 4;
      width: 0;
    }

    .input label {
      position: absolute;
      top: 10px;
      left: 0;
      z-index: 2;
      cursor: pointer;
      line-height: 60px;
    }

    .button.login {
      width: 60%;
      left: 20%;
    }

    .button.login button,
    .button button {
      height: 2.5em;
      width: 100%;
      left: 0%;
      background-color: transparent;
      border: 0.1em solid rgba(0, 0, 0, 0.1);
      font-weight: 900;
      border-radius: 0.4em;
      font-size: 1.4em;
      color: rgba(0, 0, 0, 0.2);
      /*outline:none;*/
    }

    .button.login {
      margin-top: 3em;
    }

    .button {
      margin-top: 2em;
    }

    .button button {
      background-color: #fff;
      color: #ED2553;
      /* border: none;*/
    }

    .button.login button.active {
      border: 0.1em solid transparent;
      color: #fff !important;
    }

    .button.login button.active span {
      opacity: 0;
      transform: scale(0);
      -webkit-transform: scale(0);
      -ms-transform: scale(0);
    }


    .button.login button:focus {
      color: #fff !important;
      background: #ED2553;
    }


    .button.login button:hover {
      color: #ED2553;
      border-color: #ED2553;
    }

    .button {
      margin: 1.5em 0;
      overflow: hidden;
      z-index: 2;
    }

    .button button {
      cursor: pointer;
      position: relative;
      z-index: 2;
    }

    .pass-forgot {
      width: 100%;
      float: left;
      text-align: center;
      color: rgba(0, 0, 0, 0.4);
      font-size: 1.2em;
    }

  }
}
@primary-color: #752b7d;